  /* font-family: 'Poppins', sans-serif; */
  /* font-family: 'Inter', sans-serif; */
  font-family: "Montserrat", sans-serif;
}

/* Utilities */
:root {
  --bg-third: #dcd7c9;
  --bg-secondary: #3f4e4f;
  --bg-primary: #f1ede2;
  --txt-primary: #ffff;
  --txt-secondary: #dcd7c9;
  --txt-third: #a27b5c;
}

body {
  background-color: var(--bg-primary);
}

.title {
  font-size: 40px;
  font-weight: 800;
  color: var(--bg-secondary);
  text-align: center;
}

.subtitle {
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 30px;
  line-height: 30px;
  text-align: center;
}

.hr {
  width: 100px;
  color: var(--txt-third);
  border: 3px solid var(--txt-third) !important;
  margin: 20px auto;
}

/*  */

/* End Navbar */

/* Jumbotron (Hero) */

.hero {
  display: flex;
  align-items: center;
  min-height: 650px;
  width: 100%;
  margin-top: 90px;
  text-align: center;
  background-position: center;
  background-color: black;
  background-size: cover;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.hero_inner {
  margin: 0 auto;
  max-width: 800px;
}

.hero_title {
  color: #dcd7c9;
  font-weight: 800;
  font-size: 40px;
}

.hero_tagline {
  color: #dcd7c9;
  margin-top: 16px;
  font-size: 15px;
  font-weight: 600;
}

.span_title,
.span_tagline {
  color: #a27b5c;
}

/* End Jumbotron */

/* Destination */
.card-destination-left img,
.card-destination-right img {
  object-fit: cover;
}

.card-destination-left img {
  max-height: 100%;
  height: 300px;
}

.card-destination-right img {
  max-height: 100%;
  height: 300px;
}

.content-card {
  cursor: pointer;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.item-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.card_items:hover {
  transition: transform 0.5s;
  cursor: pointer;
}

.content-card:hover {
  opacity: 100%;
  display: block;
  padding: 30px;
  text-wrap: wrap;
  text-align: center;
}

.destination-container {
  padding: 0 20px;
}

.content-card p a {
  color: #dcd7c9;
}

.content-card p a:hover {
  color: var(--txt-third);
}

/* End Destination */

/* Category */

.category {
  background-color: var(--bg-third);
  padding: 80px 0;
}

.category .title {
  color: var(--txt-third) !important;
}

.kategori_card {
  min-height: 150px;
  min-width: 150px;
  background-color: var(--bg-secondary);
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s;
}

.kategori_items :hover {
  transform: scale(1.2);
  cursor: pointer;
}

.kategori_card a:hover,
.kategori_card h5:hover,
.kategori_card img:hover {
  transform: scale(1);
}

.kategori_card img {
  max-width: 50px;
  max-height: 50px;
  object-fit: contain;
}

.kategori_card h5 {
  color: var(--txt-third);
  margin-top: 15px;
  margin-bottom: 0;
  font-weight: 600;
}

/* Service */

.service {
  background-color: var(--bg-third);
  overflow-x: hidden !important;
}

.img-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.icon-service {
  width: 110px;
  height: 100px;
  background-color: var(--bg-secondary);
  padding: 10px;
  object-fit: contain;
  border-radius: 20%;
}

.service-title {
  font-size: 22px;
  font-weight: 600;
  margin: 20px 0;
}

.service-subtitle {
  line-height: 30px;
  font-weight: 400;
}

/* Swiper */

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.swiper-button-next,
.swiper-button-prev {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px !important;
  color: var(--bg-third);
  font-weight: 900;
}

.item-top-penginapan img {
  height: 75vh;
  width: 100%;
}

.item-wisata-lainnya .card-wisata-lainnya img {
  height: 40vh;
  display: block;
  width: 1000px;
  max-width: 100%;
  object-fit: cover;
}

.item-wisata-lainnya .card-penginapan img {
  height: 40vh;
  display: block;
  width: 1000px;
  max-width: 100%;
  object-fit: cover;
  transform: scale(1);
  transition: all 0.5s ease;
}

/* start page */
.startpage {
  background-color: #2c3639;
}

.btn-start {
  background-color: var(--txt-secondary) !important;
  box-shadow: none;
  border: none;
  font-weight: 600;
}

.btn-start:hover {
  background-color: #a27b5c !important;
  border: none;
}

.btn-start i {
  color: #000 !important;
}

.btn-start:hover i {
  color: var(--txt-primary) !important;
}

/* end start page */

/*  Category Alam*/

.alamItemCard {
  padding: 120px 0;
}

.wisata-card {
  background-color: #2c3639;
  color: #dcd7c9;
}

/* map */
.card-map {
  padding: 10px 0;
  background-color: var(--bg-secondary);
  color: var(--txt-primary);
}
.card-map img {
  width: 200px;
  height: 150px;
  object-fit: cover;
}
.leaflet-popup-content-wrapper {
  background-color: var(--bg-secondary);
}
.leaflet-popup {
  background-color: transparent;
}
/* end map */

/* responsive */
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 400px) {
  .content-card h5 {
    font-size: 18px;
    font-weight: 600;
    color: #a27b5c;
    margin-bottom: 5px;
  }

  .content-card p {
    color: #dcd7c9;
    font-size: 14px;
    text-wrap: wrap;
    text-align: justify;
  }
}

@media only screen and (min-width: 400px) {
  .content-card h5 {
    font-size: 26px;
    font-weight: 600;
    color: #a27b5c;
    margin-bottom: 5px;
  }

  .content-card p {
    color: #dcd7c9;
    font-size: 14px;
    text-align: justify;
  }
}

@media only screen and (max-width: 620px) {
  .boxTopSaran {
    top: 3% !important;
    left: 12.3% !important;
  }

  .boxTopSaran h3 {
    font-size: 18px;
    padding: 10px 20px !important;
  }
}

@media only screen and (min-width: 520px) {
  .content-card h5 {
    font-size: 26px;
  }

  .content-card p {
    font-size: 16px;
  }

  .boxTopSaran {
    top: 20% !important;
    left: 5% !important;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .hero_title {
    font-size: 60px;
  }

  .hero_tagline {
    font-size: 18px;
  }

  .card-destination-left .content-card h5 {
    font-size: 30px;
  }

  .card-destination-left .content-card p {
    font-size: 20px;
  }

  .card-destination-right .content-card h5 {
    font-size: 20px;
  }

  .card-destination-right .content-card p {
    font-size: 14px;
  }

  .card_items:hover {
    transform: scale(1.05);
    z-index: 100;
  }

  .item-card p:hover,
  .item-card h5:hover {
    transform: scale(1);
  }

  .content-card:hover {
    transform: scale(1);
  }

  .card-destination-left img {
    max-height: 100%;
    height: 620px;
  }

  .card-destination-right img {
    max-height: 100%;
    height: 300px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .card-destination-left .content-card h5 {
    font-size: 30px;
  }

  .card-destination-left .content-card p {
    font-size: 20px;
  }

  .card-destination-right .content-card h5 {
    font-size: 20px;
  }

  .card-destination-right .content-card p {
    font-size: 14px;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .card-destination-left .content-card h5 {
    font-size: 30px;
  }

  .card-destination-left .content-card p {
    font-size: 20px;
  }

  .card-destination-right .content-card h5 {
    font-size: 18px;
  }

  .card-destination-right .content-card p {
    font-size: 12px;
  }

  .destination-container {
    padding: 0;
  }
}

/* end responsive */
