 background-color: rgba(0, 0, 0, 0.8);\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  opacity: 0;\r\n}\r\n\r\n.item-card {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.card_items:hover {\r\n  transition: transform 0.5s;\r\n  cursor: pointer;\r\n}\r\n\r\n.content-card:hover {\r\n  opacity: 100%;\r\n  display: block;\r\n  padding: 30px;\r\n  text-wrap: wrap;\r\n  text-align: center;\r\n}\r\n\r\n.destination-container {\r\n  padding: 0 20px;\r\n}\r\n\r\n.content-card p a {\r\n  color: #dcd7c9;\r\n}\r\n\r\n.content-card p a:hover {\r\n  color: var(--txt-third);\r\n}\r\n\r\n/* End Destination */\r\n\r\n/* Category */\r\n\r\n.category {\r\n  background-color: var(--bg-third);\r\n  padding: 80px 0;\r\n}\r\n\r\n.category .title {\r\n  color: var(--txt-third) !important;\r\n}\r\n\r\n.kategori_card {\r\n  min-height: 150px;\r\n  min-width: 150px;\r\n  background-color: var(--bg-secondary);\r\n  border-radius: 30px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  transition: transform 0.2s;\r\n}\r\n\r\n.kategori_items :hover {\r\n  transform: scale(1.2);\r\n  cursor: pointer;\r\n}\r\n\r\n.kategori_card a:hover,\r\n.kategori_card h5:hover,\r\n.kategori_card img:hover {\r\n  transform: scale(1);\r\n}\r\n\r\n.kategori_card img {\r\n  max-width: 50px;\r\n  max-height: 50px;\r\n  object-fit: contain;\r\n}\r\n\r\n.kategori_card h5 {\r\n  color: var(--txt-third);\r\n  margin-top: 15px;\r\n  margin-bottom: 0;\r\n  font-weight: 600;\r\n}\r\n\r\n/* Service */\r\n\r\n.service {\r\n  background-color: var(--bg-third);\r\n  overflow-x: hidden !important;\r\n}\r\n\r\n.img-cover {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.icon-service {\r\n  width: 110px;\r\n  height: 100px;\r\n  background-color: var(--bg-secondary);\r\n  padding: 10px;\r\n  object-fit: contain;\r\n  border-radi