  background-color: var(--bg-secondary);\r\n}\r\n.leaflet-popup {\r\n  background-color: transparent;\r\n}\r\n/* end map */\r\n\r\n/* responsive */\r\n/* Extra small devices (phones, 600px and down) */\r\n@media only screen and (max-width: 400px) {\r\n  .content-card h5 {\r\n    font-size: 18px;\r\n    font-weight: 600;\r\n    color: #a27b5c;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\n  .content-card p {\r\n    color: #dcd7c9;\r\n    font-size: 14px;\r\n    text-wrap: wrap;\r\n    text-align: justify;\r\n  }\r\n}\r\n\r\n@media only screen and (min-width: 400px) {\r\n  .content-card h5 {\r\n    font-size: 26px;\r\n    font-weight: 600;\r\n    color: #a27b5c;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\n  .content-card p {\r\n    color: #dcd7c9;\r\n    font-size: 14px;\r\n    text-align: justify;\r\n  }\r\n}\r\n\r\n@media only screen and (max-width: 620px) {\r\n  .boxTopSaran {\r\n    top: 3% !important;\r\n    left: 12.3% !important;\r\n  }\r\n\r\n  .boxTopSaran h3 {\r\n    font-size: 18px;\r\n    padding: 10px 20px !important;\r\n  }\r\n}\r\n\r\n@media only screen and (min-width: 520px) {\r\n  .content-card h5 {\r\n    font-size: 26px;\r\n  }\r\n\r\n  .content-card p {\r\n    font-size: 16px;\r\n  }\r\n\r\n  .boxTopSaran {\r\n    top: 20% !important;\r\n    left: 5% !important;\r\n  }\r\n}\r\n\r\n/* Medium devices (landscape tablets, 768px and up) */\r\n@media only screen and (min-width: 768px) {\r\n  .hero_title {\r\n    font-size: 60px;\r\n  }\r\n\r\n  .hero_tagline {\r\n    font-size: 18px;\r\n  }\r\n\r\n  .card-destination-left .content-card h5 {\r\n    font-size: 30px;\r\n  }\r\n\r\n  .card-destination-left .content-card p {\r\n    font-size: 20px;\r\n  }\r\n\r\n  .card-destination-right .content-card h5 {\r\n    font-size: 20px;\r\n  }\r\n\r\n  .card-destination-right .content-card p {\r\n    font-size: 14px;\r\n  }\r\n\r\n  .card_items:hover {\r\n    transform: scale(1.05);\r\n    z-index: 100;\r\n  }\r\n\r\n  .item-card p:hover,\r\n  .item-card h5:hover {\r\n    t